/* Button styles */
.button {
    background-color: #4CAF50; /* Green */
    border: none;
    color: white;
    padding: 10px 20px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    cursor: pointer;
    border-radius: 4px;
}

/* Hover effect */
.button:hover {
    background-color: #45a049;
}

/* Active effect */
.button:active {
    background-color: #3e8e41;
}

.dashboard-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    /* margin-bottom: 10px; */
    margin-top: 10px;
  }
  
  .dashboard-header h2 {
    margin: 0;
  }
  
  .logout-button {
    background-color: #ff5555; 
    color: white;
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
    
  }
  


  .logout-button:hover {
    background-color: #ff3333; 
  }
  

  .dashboard-button {
    background-color: #5c5ccf; 
    color: white;
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
    min-width: 150px;
    margin-right: 35px; 
  }
  .dashboard-button:hover {
    background-color: #4646d2; 
  }
 